import React from 'react'
import propTypes from 'prop-types'
import ImageLoader from 'react-load-image'
import { CircularProgress } from 'material-ui/Progress'
import { withPrefix } from 'gatsby-link'

const PreLoader = ({ style }) =>
  <div style={{
    ...style,
    height: '100vh',
    textAlign: 'center',
    lineHeight: '600px'
  }}>
    <CircularProgress
      style={{color: '#343a40'}}
      size={50}
    />
  </div>

export default class PageProvider extends React.Component {
  static propTypes = {
    number: propTypes.number.isRequired
  }

  normalize (number) {
    if (number < 10) return '00' + number;
    if (number < 100) return '0' + number;
    if (number < 1000) return '' + number;
    throw new Error(`Page number of ${number} is too high. Is that value supposed to be valid?`);
  }

  render () {
    const { number, min, max, isSingle } = this.props;
    const correctedPageNumber = this.normalize(number);
    const pageStyle = isSingle ? styles.single : styles.double;
    return number > max || number < min
      ? <img style={{...pageStyle, ...styles.image}} src={withPrefix(`/pages/default.png`)} />
      : <ImageLoader src={withPrefix(`/pages/${correctedPageNumber}.png`)}>
          <img style={{...pageStyle, ...styles.image}} />
          <PreLoader style={{...pageStyle, ...styles.image}} />
          <PreLoader style={{...pageStyle, ...styles.image}} />
        </ImageLoader>;
  }
}

const styles = {
  image: {
    background: '#fff',
    display: 'inline-block',
    margin: '0',
    verticalAlign: 'top'
  },
  single: {
    width: '100%',
  },
  double: {
    width: '50%',
  }
};



